<template>
    <div class="pages">
        <el-row class="header">
            <el-col class="filter-label" :span="2">规则编号:</el-col>
            <el-col :span="4">
                <el-input class="filter-match" v-model="data.id" placeholder="请输入"></el-input>
            </el-col>
            <el-col class="filter-label" :span="2">分组名称:</el-col>
            <el-col :span="4">
                <el-input class="filter-match" v-model="data.name" placeholder="请输入"></el-input>
            </el-col>
            <el-col class="filter-label" :span="2"> 创建日期:</el-col>
            <el-col :span="4">
                <el-date-picker
                        style="width: 100%"
                        type="datetimerange"
                        range-separator="~"
                        v-model="data.create_time"
                        start-placeholder="开始"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        format="yyyy-MM-dd"
                        end-placeholder="结束">
                </el-date-picker>
            </el-col>

            <el-col class="filter-btn" :span="6">
                <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>
            </el-col>
        </el-row>
        <el-row class="filter">
            <el-col class="filter-label" :span="2"> 排序:</el-col>
            <el-col :span="4">
                <el-input class="filter-match" v-model="data.sort" type="number" placeholder="请输入"></el-input>
            </el-col>
        </el-row>

        <el-row>
            <el-table
                    :data="res.data"
                    stripe
                    style="width: 100%">
                <el-table-column
                        prop="id"
                        label="ID">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="分组名称">
                </el-table-column>
                <el-table-column
                        prop="create_time"
                        label="创建时间">
                </el-table-column>
                <el-table-column
                        prop="sort"
                        label="排序">
                </el-table-column>

                <el-table-column
                        label="操作">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                        <el-button @click="del(scope)" type="text" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total,prev, pager, next,jumper"
                        :page-count="res.last_page"
                        @current-change="loadData"
                >
                </el-pagination>
            </div>
        </el-row>
    </div>
</template>

<script>
    import api from '../../js/api/member/group'

    export default {
        name: "group",
        data() {
            return {
                data: {
                    id: null,
                    jump_page: null,
                    name: null,
                    create_start: null,
                    create_end: null,
                    create_time: null,
                    sort:null,
                },
                res:{}
            }
        },
        mounted() {
            this.loadData(1);
        },
        methods: {
            ref() {
                this.data = {
                    id: null,
                    jump_page: null,
                    name: null,
                    create_start: null,
                    create_end: null,
                    create_time: null,
                    sort:null,
                };
                this.loadData(1)
            },
            loadData(page) {
                this.data.jump_page = page;

                if (this.data.create_time != null && this.data.create_time.length === 2) {
                    this.data.create_start = this.data.create_time[0];
                    this.data.create_end = this.data.create_time[1]
                } else {
                    this.data.create_start = null;
                    this.data.create_end = null;
                }
                api.index(this.data, res => {
                    this.res = res.data.data;
                    console.log(this.res)
                });
            },
            handleClick(item) {
                this.$router.push({
                    name: "groupAdd",
                    query: {
                        id: item.id,
                    },
                });
            },
            add() {
                this.$router.push({
                    name: "groupAdd",
                });
            },
            del(scope) {
                let _this = this;
                this.$confirm('确认删除' + scope.row.name + '?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.delete({id: scope.row.id}, () => {
                        this.$message.success('删除成功');
                        _this.loadData(1);
                    })
                })
            },
        }
    }
</script>

